import React, { useState } from 'react'
import LoginPage from './LoginPage'
import axios from 'axios';
import RegisterPage from './RegisterPage';
import WeclomePage from './WeclomePage';


const Components = () => {
    const [loggedIn, setLoggedIn] = useState(false);
    const [register, setRegister] = useState(false);
    const [login, setLogin] = useState(true);

    const handleReaponse = (val) => {
        if(val=="register"){
            setRegister(true);
            setLoggedIn(false);
            setLogin(false);
        }
        if(val=="login"){
            setLogin(true);
            setLoggedIn(false);
            setRegister(false);

        }
        if(val=="loggedIn"){
            setLoggedIn(true);
            setLogin(false);
            setRegister(false);
        }
        if(val=="logout"){
            setLogin(true);
            setLoggedIn(false);
            setRegister(false);
        }
    }
    let container ={
        width:"100%",
        height:"100vh",
        border:"1px solid black",
        // display:"inline-block",
        padding:"5rem 3rem 3rem",
        textAlign:"center",
    }
    return (
        <div style={container}>
            {login && <LoginPage handleReaponse={handleReaponse}/>}
            {register && <RegisterPage handleReaponse={handleReaponse}/>}
            {loggedIn && <WeclomePage handleReaponse={handleReaponse} />}
            {/* <div><button>register</button> <button>login</button></div> */}
        </div>
    )
}

export default Components
