import React, { useState } from 'react'
import axios from 'axios';
import ResponsePage from './ResponsePage';


const RegisterPage = (props) => {

    const [response, setResponse] = useState(false);
    const [loginUrl, setLoginUrl] = useState("https://ttmg-backend.herokuapp.com/api/auth/staffRegister");
    const [statusCode, setStatusCode] = useState();

    const [email, setEmail] = useState("");
    const idinput = (e)=>{
        console.log(e.target.value);
        setEmail(e.target.value);
    }
    const [password, setPassword] = useState("");
    const pinput = (e)=>{
        console.log(e.target.value);
        setPassword(e.target.value);
    }  
    const [name, setName] = useState("");
    const ninput = (e)=>{
        console.log(e.target.value);
        setName(e.target.value);
    } 
    const [number, setNumber] = useState();
    const pninput = (e)=>{
        console.log(e.target.value);
        setNumber(e.target.value);
    } 
    const handle =()=>{
        console.log("register handle");
        props.handleReaponse("login");
    }
    var obj ={
        "email": email,
        "password": password,
        "name":name,
        "mobile":number
    } 
    const [msg, setMsg] = useState("");
    const fetchData = async ()=>{
        if(response){
            setResponse(false);
            return;
        }
        console.log(obj);
        
        const res = await axios.post(`${loginUrl}`,obj,{ headers: { "Accept": "application/json" } }).catch((error)=>{
            if(error.response.status == 400){
                let temp = "";
                setStatusCode(400);
                error.response.data.errors.map((val)=>{
                    console.log(val.msg);
                    temp+=" \n";
                    temp+=val.msg;
                })
                console.log(msg);
                setMsg(temp);
                setResponse(true);
            }
            else if(error.response.status == 402){
                setStatusCode(402);
                setMsg(error.response.data.errors);
                setResponse(true);
            }
        });
        if(res){
            setMsg("Successfully Registered");
            setStatusCode(200);
            setResponse(true);
            setTimeout(() => {
                setResponse(false);
                props.handleReaponse("loggedIn");
            }, 3000);
            console.log(res);

        }
    }

    let container ={
        // border:"1px solid black",
        display:"inline-block",
        padding:"2rem",
        textAlign:"center",
        boxShadow:
      `0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1)`,
    }

    return (
        <div style={container}>
            
                {!response && <div>
                    <h3>Register</h3>
                    <label for="email">enter email id:</label>
                    <input type="text" placeholder="123@email.com" id="email" onChange={(e)=>idinput(e)} required></input>
                    <br></br>
                    <label for="password">enter password</label>
                    <input type="text" placeholder="password" id="password" onChange={(e)=>pinput(e)} required></input>
                    <br></br>
                    <label for="name">enter name:</label>
                    <input type="text" placeholder="name" id="name" onChange={(e)=>ninput(e)} required></input>
                    <br></br>
                    <label for="number">enter number:</label>
                    <input type="text" placeholder="1234567890" id="number" onChange={(e)=>pninput(e)} required></input>
                    <br></br>
                </div>}
                {response && <div> <h3>Status Code</h3><ResponsePage msg={msg} statusCode={statusCode}/></div>}
                    <button onClick={()=>fetchData()} >register</button>
                    <button onClick={()=>props.handleReaponse("login")}>login</button>                
        </div>
    )
}

export default RegisterPage
