import React, { useState } from 'react'
import ResponsePage from './ResponsePage';

const WeclomePage = (props) => {
    const [response, setResponse] = useState(false);
    return (
        <div>
            {!response && <div>
                <h3>welcome page</h3>
                <button onClick={()=>setResponse(true)}>Logout</button>
            </div>}
            {response && <div><ResponsePage statusCode={"logged out successfully"}/> <button onClick={()=>props.handleReaponse("logout")}>login</button> </div>}
        </div>
    )
}

export default WeclomePage
