import React, { useState } from 'react'
import axios from 'axios';
import ResponsePage from './ResponsePage';


const LoginPage = (props) => {
    const [response, setResponse] = useState(false);
    const [email, setEmail] = useState("");
    const [statusCode, setStatusCode] = useState();
    const idinput = (e)=>{
        console.log(e.target.value);
        setEmail(e.target.value);
    }
    const [password, setPassword] = useState("");
    const pinput = (e)=>{
        console.log(e.target.value);
        setPassword(e.target.value);
    }  
    var obj ={
        "email": email,
        "password": password
    } 
    const [msg, setMsg] = useState("");
    const [loginUrl, setLoginUrl] = useState("https://ttmg-backend.herokuapp.com/api/auth/staffLogin");
    const fetchData = async ()=>{
        if(response){
            setResponse(false);
            return;
        }
        console.log(obj);
        
        const res = await axios.post(`${loginUrl}`,obj,{ headers: { "Accept": "application/json" } }).catch((error)=>{
            if(error.response.status == 400){
                let temp = "";
                setStatusCode(400);
                error.response.data.errors.map((val, index)=>{
                    temp+="\n";
                    temp+=val.msg;
                })
                console.log(msg);
                setMsg(temp);
                console.log(msg);
                setResponse(true);
            }
            else if(error.response.status == 401){
                setStatusCode(401);
                setMsg(error.response.data.errors);
                setResponse(true);
            }
        });
        if(res){
            props.handleReaponse("loggedIn");
            // console.log(res);

        }
    }


    let container ={
        // border:"1px solid black",
        display:"inline-block",
        padding:"2rem",
        textAlign:"center",
        boxShadow:
      `0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1)`,
    }


    return (
        <div style={container}>
            
                {!response && <div>
                    <h3>login</h3>
                    <label for="email">enter email id:</label>
                    <input type="text" placeholder="123@email.com" id="email" onChange={(e)=>idinput(e)} required></input>
                    <br></br>
                    <label for="password">enter password</label>
                    <input type="text" placeholder="password" id="password" onChange={(e)=>pinput(e)} required></input>
                    <br></br>
                </div>}
                    {response && <div> <h3>Status Code</h3><ResponsePage msg={msg} statusCode={statusCode}/></div>}
                    <button onClick={()=>fetchData()}>login</button>
                    <button onClick={()=>props.handleReaponse("register")}>register</button>
                
                {/* {response && <ResponsePage msg={msg}/>} */}
        </div>
    )
}

export default LoginPage
